/* src/app/home/home.css */
.home-container {
  display: flex;
  height: 100vh;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  padding: 0 5%;
  background-color: #f8f9fa;
  position: relative;
  overflow: hidden;
}
.home-container {
  display: flex;
  height: 100vh;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  padding: 0 5%;
  background-color: #f8f9fa;
}
.home-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.home-left h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
  color: #343a40;
}
.home-left p {
  font-size: 1.5rem;
  color: #6c757d;
}
.home-container {
  display: flex;
  height: 100vh;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  padding: 0 5%;
  background-color: #f8f9fa;
  position: relative;
  overflow: hidden;
}
.home-left {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  z-index: 2;
}
.home-left h1 {
  font-size: 3rem;
  margin-bottom: 1rem;
  color: #343a40;
}
.home-left p {
  font-size: 1.5rem;
  color: #6c757d;
}
.home-left-content {
  padding-top: 60px;
  padding-left: 30px;
  padding-right: 30px;
  text-align: left;
  margin-top: 100px;
  transform: translateY(-100px);
}
.home-title {
  font-size: 3.2rem;
  font-weight: 700;
  color: #1e293b;
  margin-bottom: 1rem;
}
.home-description {
  font-size: 1.2rem;
  color: #4b5563;
  line-height: 1.7;
  max-width: 500px;
  margin-bottom: 1.5rem;
}
.home-quote {
  font-style: italic;
  color: #6b7280;
  font-size: 0.95rem;
}
.home-button {
  padding: 12px 24px;
  font-size: 1rem;
  border-radius: 8px;
  background-color: #4f46e5;
  border: none;
  transition: all 0.3s ease;
}
.home-button:hover {
  background-color: #4338ca;
}
.home-right {
  flex: 1;
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  padding-top: 40px;
  padding-right: 20px;
  z-index: 1;
}
.home-right img {
  max-width: 90%;
  height: auto;
  max-height: 80vh;
  transform: translateY(-80px);
}
.text-xl {
  font-size: 1.25rem;
  line-height: 1.75rem;
  font-weight: 600;
  color: #facc15;
}
.explore-button {
  background-color: #d90429;
  color: #ffffff;
  border: none;
  padding: 12px 24px;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 4px 12px rgba(217, 4, 41, 0.4);
}
.explore-button:hover {
  background-color: #a00322;
  transform: translateY(-2px);
  box-shadow: 0 6px 16px rgba(217, 4, 41, 0.6);
}
@media (max-width: 768px) {
  .home-container {
    flex-direction: column;
    height: auto;
    padding: 1.5rem;
    text-align: center;
  }
  .home-left-content,
  .home-right {
    width: 100%;
    padding: 1rem;
  }
  .home-title {
    font-size: 1.5rem;
  }
  .home-description,
  .home-quote {
    font-size: 1rem;
  }
  .home-right img {
    max-width: 80%;
    margin-top: 1rem;
  }
  .home-button {
    text-align: center;
  }
  .home-left-content {
    padding-top: 60px;
    padding-left: 30px;
    padding-right: 30px;
    text-align: left;
    margin-top: 100px;
    transform: translateY(-100px);
  }
}
/*# sourceMappingURL=home.css.map */
